.notfound {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-block-start: 95px;
  justify-content: center;
}

.notfound__items {
  display: flex;
  align-items: center;
  gap: 10px;
  padding-block: 15.7px;
  padding-inline: 23px;
  border: solid 1.5px #337d5a;
}

.notfound__link {
  font-family: "Raleway", sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 100%;
  text-decoration: none;
  color: #337d5a;
  font-weight: 700;
}

.notfound__title {
  font-size: 244px;
  letter-spacing: 3px;
  font-family: "Montserrat";
  line-height: 1;
  font-weight: 900;
  background-image: url("../../img/404/404.png");
  -webkit-background-clip: text;
  background-clip: text;
  background-position: center;
  color: transparent;
}

.notfound__text {
  font-family: "Raleway", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 100%;
  font-weight: 500;
  line-height: 30px;
  margin-block-start: -6px;
  margin-inline-start: 4px;
  margin-block-end: 17px;
}

.map__404 {
  padding-block-start: 214px;
}